import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { FormsModule }  from '@angular/forms';

import { ImageLinkerService } from '../services/image-linker.service';
import { res } from '../services/image-linker.service';
export class itemSubmit {
  name: string;
  id: number;
  desc?: string;
  toString():string {
    return this.id+","+this.name+","+this.desc;
  }
}

@Component({
  selector: 'app-form-submit',
  templateUrl: './form-submit.component.html',
  styleUrls: ['./form-submit.component.css']
})
export class FormSubmitComponent implements OnInit {
  mItem: itemSubmit[] = itemArr;
  mdefaultItem: itemSubmit = this.mItem[0];
  submitted = false;
  isLoading = false;
  isDisplay = false;
  information = "START GET IMAGES!"
  txtInput: any;
  linkResult: res;

  isError = false;
  errorMsg: string;

  constructor(private http: Http,
              private imageService: ImageLinkerService) { }

  ngOnInit() {
    this.txtInput="https://worldcosplay.net/photo/6168137";
  }

  onSubmit(item:itemSubmit) {
    this.submitted==false?this.submitted=true:this.submitted=false;
    this.isLoading = true;
    this.imageService.getLinks(item.id, this.txtInput)
                      .then(todos => {
                            this.submitted = true;
                            this.linkResult = todos;
                            this.isLoading=false;}
                      ).catch(
                            error => {
                              this.isError=true;
                              this.errorMsg=<any>error;
                              this.isLoading=false;
                            });
  }
  regexURL: string = "(https:\/\/)?worldcosplay\.net\/photo\/([1-9])+$";
  regexNo: string = "^[1-9]+$";
  validationInput(item:itemSubmit): boolean {
    if(this.mdefaultItem.id===0) {
      if(this.txtInput.match(this.regexURL)) {
        this.isError = false;
        return true;
      } else {
        this.errorMsg = "URL isn't match!";
      }
    } 
    else if(this.txtInput.match(this.regexNo)) {
        this.isError = false;
        return true;
    } else {
      this.errorMsg = "WorldCosplay No. not found!";
    }
    this.isError=true;
    return false;
  }

  onChange(event) {
    if(event.id==0) this.txtInput="https://worldcosplay.net/photo/6168137";
    else this.txtInput="328386";
    this.resertValue();
  }
  onError(): void {
    this.submitted = false;
    this.isLoading = false;

    this.isError = false;
    this.errorMsg = "";
  }
  resertValue():void {
    this.submitted=false;    
    this.isLoading = false;
    this.isError = false;
    this.errorMsg = "";
  }
  displayDownloader(): void {
    this.isDisplay = !this.isDisplay;
    this.information==="START GET IMAGES!"?this.information="HIDE":this.information="START GET IMAGES!";
  }
  stringResult(array: res[]): string {
    let out:string ="";
    array.forEach(element => {
      out=out+""+element+"\n";
    });
    return out;
  }
}

export var itemArr: itemSubmit[] = [{
    name: 'Link',
    id: 0,
    desc: 'Ex: https://worldcosplay.net/photo/6123744'
  }, {
    name: 'Submissions',
    id: 1,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Cosplays',
    id: 2,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Photographer',
    id: 3,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Touch-Up Artist',
    id: 4,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Props Made',
    id: 5,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Make-up Artist',
    id: 6,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Collaborations',
    id: 7,
    desc: 'WorldCosplay No.'
  }, {
    name: 'Favorites',
    id: 8,
    desc: 'WorldCosplay No.'
  }];


