import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';

var request = require('request');
var cheerio = require('cheerio');
var Q = require('q');

export class res {
  option?: number;
  imgs?: string[];
  error: string;

  constructor(option: number, imgs: string[], error: string) {
    this.option = option;
    this.imgs = imgs;
    this.error = error;
  }
}

@Injectable()
export class ImageLinkerService {
  private todosUrl = 'https://api-wcpd.herokuapp.com/api?'
  constructor(private http: Http) { }

  getLinks(option: number, data: string): Promise<res> {
    if(option == 0) return this.getImgFromLink(this, data).then(data => new res(0, data.toString().split(','), 'null'));
    else {
      return this.getListOfImage(this, option, data).then(data => new res(option, data.toString().split(','), 'null'));
    }
  }

  getImgFromLink(imageLinkerService, link) {
    return new Promise(function (resolve, reject) {
      request({
        uri: link
        // , method: 'GET',
        // headers: {
        //   'Access-Control-Allow-Origin': '*',
          // 'Access-Control-Allow-Headers': 'X-Requested-With, X-HTTP-Method-Override, Content-Type, Accept',
        //   'Access-Control-Allow-Methods': 'POST, GET, PUT, DELETE, OPTIONS',
        //   'Access-Control-Max-Age': '86400'
        // }
      }, function (err, res, body) {
        if (err) {
          console.log('Error! ', err);
          reject(err);
        } else {
          let html = cheerio.load(body, {
            xmlMode: true
          });
          let img = html('img.img').first().attr('src');
          if (img === null || img === undefined) reject('Image not found!');
          else {
            imageLinkerService.getLinkHQ(img).then(data => {
              resolve(data);
            })
          }
        }
      });
    });
  }

  private getLinkHQ(link) {
    return new Promise(function (resolve, reject) {
      link = link.substr(0, link.lastIndexOf('-'));
      request({
        method: 'HEAD',
        uri: link + '-3000.jpg'
      }, function (err3, res, body) {
        if (err3) {
          request({
            method: 'HEAD',
            uri: link + '-1200.jpg'
          }, function (err2, res, body) {
            if (err2) {
              request({
                method: 'HEAD',
                uri: link + '-900.jpg'
              }, function (err9, res, body) {
                if (err9) {
                  resolve(link + '-740.jpg');
                } else resolve(link + '-900.jpg');
              });
            } else resolve(link + '-1200.jpg');
          });
        } else resolve(link + '-3000.jpg');
      });
    });
  }

  getImgFromJSON(imageLinkerService, link) {
    return new Promise(function (resolve, reject) {
      request({
        uri: link
      }, function (err, res, body) {
        if (err) {
          reject(err);
        } else {
          let html = JSON.parse(body);
          if (html.has_error == 1 || html.list.length == 0) reject(html.message); 
          var fn = function asyncMap(photo) {        
            return imageLinkerService.getLinkHQ(photo.photo.sq150_url.replace('/sq150', ''));
          };
          let imgs = Promise.all(html.list.map(fn)).then(function (data) {
            return Promise.all(data);
          }).catch(error => {
            reject(error);
          });

          imgs.then(function (datas) {
            resolve({
              'imgs': datas,
              'can_next': html.pager.next_page
            });
          }).catch(error => {
            reject(error);
          });
        }
      });
    });
  }

  formatURL(fmtstr, ...value) {
    return fmtstr.replace(/{(\d)+}/g, function (match, index) {
      return value[index];
    });
  }

  getListOfImage(imageLinkerService, option, data) {
    return new Promise(function (resolve, reject) {
      let link = 'https://worldcosplay.net/api/member/';
      if (option == 8) //Favorites
        link += imageLinkerService.formatURL("good?member_id={0}&limit=16&rows=16", data);
      else if (option == 1) //Submissions
        link += imageLinkerService.formatURL("photos.json?limit=16&member_id={0}&p3_photo_list=true", data);
      else {
        link += "role_photos.json?limit=16&member_id={0}&p3_photo_list=true&photo_context=member_role:{0}:{1}&role_id={1}";
        switch (option) {
          case 2: //Cosplays
            {
              link = imageLinkerService.formatURL(link, data, 1);
              break;
            }
          case 3: //Photographer
            {
              link = imageLinkerService.formatURL(link, data, 2);
              break;
            }
          case 4: //TouchUpArtist
            {
              link = imageLinkerService.formatURL(link, data, 3);
              break;
            }
          case 5: //PropsMade
            {
              link = imageLinkerService.formatURL(link, data, 4);
              break;
            }
          case 6: //MakeUpArtist
            {
              link = imageLinkerService.formatURL(link, data, 5);
              break;
            }
          case 7: //Collaborations
            {
              link = imageLinkerService.formatURL(link, data, 6);
              break;
            }
            // default:
            //   reject('Option is not valid!');
        }
      }
      link += "&page={0}";
      // Usage
      let imgs = [];
      var page = 1;
      promiseWhile(function () {
        return page != null;
      }, function () {
        return imageLinkerService.getImgFromJSON(imageLinkerService, imageLinkerService.formatURL(link, page)).then(function (data) {
          imgs = [].concat(imgs, data.imgs);
          page = data.can_next;
        });
      }).then(function () {
        resolve(imgs);
      }).catch(function (error) {
        reject(error);
      }).done();
      
    function promiseWhile(condition, body) {
      var done = Q.defer();

      function loop() {
        if (!condition()) return done.resolve();
        Q.when(body(), loop, done.reject);
      }
      Q.nextTick(loop);
      return done.promise;
    }
    });
  }  
}



